<template>
    <div>
        <header class="ezcache-screen-header">
            <svg viewBox="0 0 24 24">
                <path d="M22.7,19L13.6,9.9C14.5,7.6 14,4.9 12.1,3C10.1,1 7.1,0.6 4.7,1.7L9,6L6,9L1.6,4.7C0.4,7.1 0.9,10.1 2.9,12.1C4.8,14 7.5,14.5 9.8,13.6L18.9,22.7C19.3,23.1 19.9,23.1 20.3,22.7L22.6,20.4C23.1,20 23.1,19.3 22.7,19Z" />
            </svg>
            {{ $wp.trans('advanced_settings') }}
        </header>

        <form method="post" @submit.prevent="saveSettings">
            <TogglePanel :showSpinner="isLoading">
                <template slot="title">{{ $wp.trans('performance_settings') }}</template>

                <div class="row">
                    <div class="col-12 my-2">
                        <input type="checkbox" class="checkbox" :id="$id('combine_css_footer')" v-model="settings.combine_css_footer" :disabled="!settings.combine_css || isLoading || isSaving">
                        <label :for="$id('combine_css_footer')" v-text="$wp.trans('combine_css_footer')"></label>

                        <p class="description" v-text="$wp.trans('combine_css_footer_description')"></p>
                        <p v-if="settings.combine_css === false" class="notice notice-info notice-inline" v-text="$wp.trans('combine_css_footer_requires_combine_css_notice')"></p>
                    </div>
                </div>

                <div class="row mt-3">
                    <div class="col-12 my-2">
                        <label :for="$id('critical_css')">{{ $wp.trans('critical_css') }}</label>

                        <p class="description" v-text="$wp.trans('critical_css_description')"></p>
                        <textarea :id="$id('critical_css')" class="large-text ltr" v-model="settings.critical_css" rows="7" cols="15"></textarea>
                        <p class="description">
                            <a class="button button-small" href="https://web.dev/extract-critical-css/" rel="noopener noreferrer" target="_blank" v-text="$wp.trans('critical_css_more_information')"></a>
                            <a class="button button-small" href="https://www.corewebvitals.io/tools/critical-css-generator" rel="noopener noreferrer" target="_blank" v-text="$wp.trans('critical_css_online_tool')"></a>
                        </p>
                    </div>
                </div>

                <div class="row mt-3">
                    <div class="col-12 my-2">
                        <label :for="$id('excluded_minify_files')">{{ $wp.trans('excluded_minify_files') }}</label>

                        <p class="description" v-text="$wp.trans('excluded_minify_files_description')"></p>
                        <textarea :id="$id('excluded_minify_files')" class="large-text ltr" v-model="settings.excluded_minify_files" rows="7" cols="15" :placeholder="$wp.trans('excluded_minify_files_placeholder')"></textarea>
                    </div>
                </div>
            </TogglePanel>

            <TogglePanel :showSpinner="isLoading">
                <template slot="title">{{ $wp.trans('cache_bypass_settings') }}</template>

                <div class="row">
                    <div class="col-12 my-2">
                        <label v-text="$wp.trans('bypass_cache_title')"></label>
                        <span class="mx-2">
                            <button type="button" class="wpb-button wpb-button-sm wpb-button-outlined wpb-button-primary" v-text="$wp.trans('select_all')" @click.prevent="updateSettings(true, 'single', 'pages', 'frontpage', 'home', 'archives', 'tag', 'category', 'feed', 'search', 'author')"></button>
                            <button type="button" class="wpb-button wpb-button-sm wpb-button-outlined wpb-button-primary" v-text="$wp.trans('select_none')" @click.prevent="updateSettings(false, 'single', 'pages', 'frontpage', 'home', 'archives', 'tag', 'category', 'feed', 'search', 'author')"></button>
                        </span>
                    </div>

                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_single')" v-model="settings.bypass_cache.single" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_single')" v-text="$wp.trans('bypass_cache_single')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_pages')" v-model="settings.bypass_cache.pages" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_pages')" v-text="$wp.trans('bypass_cache_pages')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_frontpage')" v-model="settings.bypass_cache.frontpage" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_frontpage')" v-text="$wp.trans('bypass_cache_frontpage')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_home')" v-model="settings.bypass_cache.home" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_home')" v-text="$wp.trans('bypass_cache_home')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_archives')" v-model="settings.bypass_cache.archives" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_archives')" v-text="$wp.trans('bypass_cache_archives')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_tag')" v-model="settings.bypass_cache.tag" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_tag')" v-text="$wp.trans('bypass_cache_tag')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_category')" v-model="settings.bypass_cache.category" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_category')" v-text="$wp.trans('bypass_cache_category')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_feed')" v-model="settings.bypass_cache.feed" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_feed')" v-text="$wp.trans('bypass_cache_feed')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_search')" v-model="settings.bypass_cache.search" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_search')" v-text="$wp.trans('bypass_cache_search')"></label>
                    </div>
                    <div class="col-12 mt-1">
                        <input type="checkbox" class="checkbox" :id="$id('bypass_cache_author')" v-model="settings.bypass_cache.author" :disabled="isLoading || isSaving">
                        <label class="font-weight-normal" :for="$id('bypass_cache_author')" v-text="$wp.trans('bypass_cache_author')"></label>
                    </div>
                </div>

                <div class="row mt-3">
                    <div class="col-12 my-2">
                        <label :for="$id('rejected_uri')">{{ $wp.trans('rejected_uri') }}</label>

                        <p class="description" v-text="$wp.trans('rejected_uri_description')"></p>
                        <textarea :id="$id('rejected_uri')" class="large-text ltr" v-model="settings.rejected_uri" rows="7" cols="15" :placeholder="$wp.trans('rejected_uri_placeholder')"></textarea>
                        <p class="description" v-text="$wp.trans('rejected_uri_wildcard')"></p>
                    </div>
                </div>

                <div class="row mt-3">
                    <div class="col-12 my-2">
                        <label :for="$id('rejected_user_agent')">{{ $wp.trans('rejected_user_agent') }}</label>

                        <p class="description" v-text="$wp.trans('rejected_user_agent_description')"></p>
                        <textarea :id="$id('rejected_user_agent')" class="large-text ltr" v-model="settings.rejected_user_agent" rows="7" cols="15" :placeholder="$wp.trans('rejected_user_agent_placeholder')"></textarea>
                    </div>
                </div>

                <div class="row mt-3">
                    <div class="col-12 my-2">
                        <label :for="$id('rejected_cookies')">{{ $wp.trans('rejected_cookies') }}</label>

                        <p class="description" v-text="$wp.trans('rejected_cookies_description')"></p>
                        <textarea :id="$id('rejected_cookies')" class="large-text ltr" v-model="settings.rejected_cookies" rows="7" cols="15" :placeholder="$wp.trans('rejected_cookies_placeholder')"></textarea>
                    </div>
                </div>
            </TogglePanel>

            <p class="submit">
                <button type="submit" class="wpb-button wpb-button-lg wpb-button-primary" :disabled="isLoading || isSaving || isDeleting">
                    <Loader v-if="isSaving" color="#fff"></Loader>
                    <svg v-else viewBox="0 0 24 24">
                        <path d="M15,9H5V5H15M12,19A3,3 0 0,1 9,16A3,3 0 0,1 12,13A3,3 0 0,1 15,16A3,3 0 0,1 12,19M17,3H5C3.89,3 3,3.9 3,5V19A2,2 0 0,0 5,21H19A2,2 0 0,0 21,19V7L17,3Z" />
                    </svg>
                    {{ $wp.trans('save_settings') }}
                </button>
            </p>

            <p v-if="!isSaving && !isLoading">
                <button type="button" class="wpb-button wpb-button-sm wpb-button-outlined wpb-button-danger wpb-button-noborder" :disabled="isLoading || isSaving || isDeleting" @click="deleteSettings">
                    <Loader v-if="isDeleting" color="#fff"></Loader>
                    <svg v-else viewBox="0 0 24 24">
                        <path d="M9,3V4H4V6H5V19A2,2 0 0,0 7,21H17A2,2 0 0,0 19,19V6H20V4H15V3H9M7,6H17V19H7V6M9,8V17H11V8H9M13,8V17H15V8H13Z" />
                    </svg>
                    {{ $wp.trans('reset_settings') }}
                </button>
            </p>
        </form>
    </div>
</template>

<script>
    import Loader from "../Components/Loader";
    import TogglePanel from "../Components/TogglePanel";
    import API from "../Utilities/Api";

    export default {
        components: {Loader, TogglePanel},
        data() {
            return {
                isLoading: true,
                isSaving: false,
                isDeleting: false,

                settings: {
                    bypass_cache: {
                        single: null,
                        pages: null,
                        frontpage: null,
                        home: null,
                        archives: null,
                        tag: null,
                        category: null,
                        feed: null,
                        search: null,
                        author: null
                    },
                    rejected_uri: null,
                    rejected_user_agent: null,
                }
            };
        },
        async mounted() {
            this.isLoading = true;

            try {
                let response = await API.get('settings');
                this.settings = Object.assign(this.settings, response.data.data);
            } catch(error) {
                console.error(error)
            }

            this.isLoading = false;
        },
        methods: {
            async deleteSettings() {
                let results = await this.confirm(this.$wp.trans('reset_settings'), this.$wp.trans('confirm_reset_settings'), 'danger', this.$wp.trans('reset'));
                if(!results.value) {
                    return;
                }

                this.isDeleting = true;

                try {
                    let response = await API.delete('settings');
                    this.settings = Object.assign(this.settings, response.data.data)
                    this.toast(this.$wp.trans('settings_saved'), 'success')
                } catch (e) {
                    console.error(e);
                    this.toast(this.$wp.trans('error_saving_settings') + ':' + e.message, 'error');
                }

                this.isDeleting = false;
            },
            async saveSettings() {
                this.isSaving = true;

                try {
                    await API.patch('settings', this.settings);
                    this.toast(this.$wp.trans('settings_saved'), 'success')
                } catch (e) {
                    console.error(e);
                    this.toast(this.$wp.trans('error_saving_settings') + ':' + e.message, 'error');
                }

                this.isSaving = false;
            },
            updateSettings(value, ...fields) {
                for(let field of fields) {
                    this.settings.bypass_cache[field] = value;
                }
            }
        }
    };
</script>

<style>
    .widget + .widget {
        margin-top: 40px;
    }
</style>
